import React from 'react';
import { Switch, Route } from 'react-router';

import { Home } from '../Containers/Home'

export const Routes = () => {
    return (
        <Switch>
            <Route exact path="/" component={Home}/>
            <Route path="/home" component={Home}/>
        </Switch>
    )
}




